import React from 'react';
import Container from '@material-ui/core/Container';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Box, Paper, Link } from '@material-ui/core';

import Highlight from 'react-highlight.js';
import SimpleTabs from '../components/common/SimpleTabs';

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: 15,
  },
}));

export default function Page20201102(props) {
  const classes = useStyles();

  var allTabs = [];
  allTabs.push('Linux');
  allTabs.push('Windows');

  var downloadTabPanels = [];
  downloadTabPanels.push(
    <Highlight language={'Bash'}>
      {
        '$ wget https://files.minecraftforge.net/maven/net/minecraftforge/forge/1.16.3-34.1.0/forge-1.16.3-34.1.0-installer.jar'
      }
    </Highlight>
  );
  downloadTabPanels.push(<Highlight language={'Bash'}>{'Download from web page'}</Highlight>);

  var installTabPanels = [];
  installTabPanels.push(
    <Highlight language={'Bash'}>
      {'$ java -jar forge-1.16.3-34.1.0-installer.jar --installServer\n\n' +
        'JVM info: Oracle Corporation - 1.8.0_261 - 25.261-b12\n' +
        'java.net.preferIPv4Stack=true\n' +
        'Found java version 1.8.0_261\n' +
        'Target Directory: .\n' +
        'Extractiung main jar:\n' +
        '  Extracted successfully\n' +
        'Considering minecraft server jar\n' +
        '  Downloading library from https://launcher.mojang.com/v1/objects/f02f4473dbf152c23d7d484952121db0b36698cb/server.jar\n' +
        '    Download completed: Checksum validated.\n' +
        'Downloading libraries\n' +
        'Considering library net.minecraftforge:forge:1.16.3-34.1.0\n' +
        '  Extracting library from /maven/net/minecraftforge/forge/1.16.3-34.1.0/forge-1.16.3-34.1.0.jar\n' +
        '    Extraction completed: Checksum validated.\n' +
        'Considering library org.ow2.asm:asm:7.2\n\n...\n\n' +
        '  Patching net/minecraft/client/renderer/entity/ItemRenderer 1/1\n' +
        '  Patching net/minecraft/client/gui/screen/CreateWorldScreen$GameMode 1/1\n' +
        '  Output: /usr/minecraft_1.16.3/./libraries/net/minecraftforge/forge/1.16.3-34.1.0/forge-1.16.3-34.1.0-server.jar Checksum Validated: 3bb761476e8ee186a7980195542f43cc24b26c46\n' +
        'The server installed successfully, you should now be able to run the file forge\n' +
        'You can delete this installer file now if you wish'}
    </Highlight>
  );
  installTabPanels.push(
    <Highlight language={'Bash'}>
      {'Double click the jar file and select install as server'}
    </Highlight>
  );

  var startupTabPanels = [];
  startupTabPanels.push(
    <Highlight language={'Bash'}>
      {'$ vi ./startMine.sh\n\n#!/bin/bash\n' +
        'java -Xms1024M -Xmx4096M -jar /usr/minecraft_1.16.3/forge-1.16.3-34.1.0.jar nogui\n\n' +
        '$ chmod +x startMine.sh'}
    </Highlight>
  );
  startupTabPanels.push(
    <Highlight language={'Bash'}>
      {'$ vi ./startMine.bat\n\n' +
        'java -Xms1024M -Xmx4096M -jar /usr/minecraft_1.16.3/forge-1.16.3-34.1.0.jar nogui\n\n'}
    </Highlight>
  );

  var startTabPanels = [];
  startTabPanels.push(<Highlight language={'Bash'}>{'$ ./startMine.sh'}</Highlight>);
  startTabPanels.push(<Highlight language={'Bash'}>{'$ startMine.bat'}</Highlight>);

  return (
    <React.Fragment>
      <Container component="main" maxWidth="md">
        <Typography component="div">
          <Box textAlign="left" fontSize="h6.fontSize" mt={2}>
            2020-11-02
          </Box>
        </Typography>
        <Typography component="div">
          <Box textAlign="center" fontSize="h4.fontSize" mt={2}>
            How to set up a modded minecraft server
          </Box>
        </Typography>
        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            You can set up minecraft in your machine and invite others in the LAN to play together.
            However your machine will act as both server and client. It uses your system resource so
            we might have lag sometime. If you have a server, you can install minecraft there. Then
            it won't take your system resource when play. Here it's how.
          </Box>
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            1. Download Forge installer from offical website{' '}
            <Link href="http://files.minecraftforge.net/" target="_blank" rel="noreferrer">
              http://files.minecraftforge.net/
            </Link>
          </Box>
        </Typography>

        <SimpleTabs tabLabels={allTabs} tabContents={downloadTabPanels} />

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            2. Run the installer
          </Box>
        </Typography>

        <SimpleTabs tabLabels={allTabs} tabContents={installTabPanels} />

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            3. Create start up script and assign execute permission for Linux
          </Box>
        </Typography>

        <SimpleTabs tabLabels={allTabs} tabContents={startupTabPanels} />

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            4. Create eula.txt to accept the agreement
          </Box>
        </Typography>

        <Highlight language={'Bash'}>
          {'$ vi ./eula.txt\n\n' +
            '#By changing the setting below to TRUE you are indicating your agreement to our EULA (https://account.mojang.com/documents/minecraft_eula).\n' +
            '#Sun Nov 01 13:55:17 EST 2020\neula=true\n\n'}
        </Highlight>

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            5. Create mods folder, download and place the mod jars to the folder. Version has to be
            matched.
          </Box>
        </Typography>

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            6. Start the server
          </Box>
        </Typography>

        <SimpleTabs tabLabels={allTabs} tabContents={startTabPanels} />

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            7. Find the port for the server by checking the log
          </Box>
        </Typography>

        <Highlight language={'Bash'}>
          {
            '...\n[14:02:25] [Server thread/INFO] [minecraft/DedicatedServer]: Starting Minecraft server on *:25565\n...\n'
          }
        </Highlight>

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            8. You can change the default port by editting server.properties
          </Box>
        </Typography>

        <Highlight language={'Bash'}>{'...\nserver-port=25565\n...\n'}</Highlight>

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            9. Open the port in Linux if you have firewall
          </Box>
        </Typography>

        <Highlight language={'Bash'}>
          {'## Command to list active zone\n$ sudo firewall-cmd --get-active-zones\n\n' +
            '## Command to open the port on firewall\n$ sudo firewall-cmd --zone=home --add-port=25565/tcp --permanent\n\n' +
            '## Command to reload setting\n$ sudo firewall-cmd --reload\n\n' +
            '## Command to restart firewall\n$ sudo systemctl restart firewalld\n\n'}
        </Highlight>

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            10. You should be able to connect to Minecraft server
          </Box>
        </Typography>

        <Highlight language={'Bash'}>
          {'## Command to stop minecraft server\n[14:03:33] [Server thread/INFO] [minecraft/DedicatedServer]: Done (66.692s)! For help, type "help"\n' +
            '> stop\n\n'}
        </Highlight>
      </Container>
    </React.Fragment>
  );
}
