import React from 'react';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import { useScrollTrigger, Zoom, Fab, Typography, Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Header from '../components/common/Header';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import Page20201026 from './Page20201026';
import Page20201028 from './Page20201028';
import Page20201101 from './Page20201101';
import Page20201102 from './Page20201102';

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  carouselBox: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'white',
  },
  root: {
    position: 'fixed',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
}));

export default function Home(props) {
  const classes = useStyles();

  function ScrollTop(props) {
    const { children, window } = props;
    const classes = useStyles();
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    const trigger = useScrollTrigger({
      target: window ? window() : undefined,
      disableHysteresis: true,
      threshold: 100,
    });

    const handleClick = (event) => {
      const anchor = (event.target.ownerDocument || document).querySelector('#back-to-top-anchor');

      if (anchor) {
        anchor.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }
    };

    return (
      <Zoom in={trigger}>
        <div onClick={handleClick} role="presentation" className={classes.root}>
          {children}
        </div>
      </Zoom>
    );
  }

  return (
    <div>
      <div id="back-to-top-anchor"></div>
      <Header />

      <Container component="main" maxWidth="sm">
        <CssBaseline />
        <Typography component="div">
          <Box textAlign="center" fontSize="h3.fontSize" mt={5}>
            Jacky's blog
          </Box>
        </Typography>
        <Typography component="div" color="textSecondary">
          <Box textAlign="center" fontSize="body1.fontSize" lineHeight={2}>
            And a new philosophy emerged called quantum physics, which suggest that the individuals
            function is to inform and be informed. You really exist only when youre in a field
            sharing and exchanging information. You create the realities you inhabit.
          </Box>
        </Typography>
        <div className={classes.paper}></div>
      </Container>

      <Page20201102 />
      <Page20201101 />
      <Page20201028 />
      <Page20201026 />

      <ScrollTop {...props}>
        <Fab color="secondary" size="small" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
    </div>
  );
}
