import React from 'react';
import Container from '@material-ui/core/Container';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Box, Grid, Divider, Paper } from '@material-ui/core';
import PASSPORT_DIAM from '../../public/assets/images/P20201028/fmh-PassportJS.png';
import GOOGLE_SETUP1 from '../../public/assets/images/P20201028/google1.png';
import GOOGLE_SETUP2 from '../../public/assets/images/P20201028/google2.png';
import Highlight from 'react-highlight.js';

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: 15,
  },
}));

export default function Page20201028(props) {
  const classes = useStyles();

  return (
    <React.Fragment>
      <Container component="main" maxWidth="md">
        <Box textAlign="center" fontSize="h6.fontSize" mt={5}>
          ---
        </Box>
        <Typography component="div">
          <Box textAlign="left" fontSize="h6.fontSize" mt={2}>
            2020-10-28
          </Box>
        </Typography>
        <Typography component="div">
          <Box textAlign="center" fontSize="h4.fontSize" mt={2}>
            PassportJS in NodeJS handle Oauth2
          </Box>
        </Typography>
        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            Today I'm going to share how to configure PassportJS for Oauth2 authentication. The
            reason to using it is pretty straightforward. We don't want to handle authentication in
            our application. It's more secure and makes the application more focus on the business
            functions.
          </Box>
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            The environment I have is, I have a NodeJS application as my backend. It provides
            backend API functions for my two frontend. One is mobile, the other is web. The NodeJS
            handles oauth2.0 for both mobile and web. We will use Google as example to show how to
            set it up. Faceboob and Github auth is similar.
          </Box>
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            Below it's the high level diagram to illustrate all components.
          </Box>
        </Typography>
        <Box mt={5} mb={5}>
          <Divider variant="middle" />
        </Box>
        <img alt="passport js system component" src={PASSPORT_DIAM} width="100%" />
        <Box mt={5} mb={5}>
          <Divider variant="middle" />
        </Box>
        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" mt={2}>
            Let's start..
          </Box>
          <Box textAlign="Left" fontSize="body1.fontSize" fontWeight="fontWeightBold" mt={2} mb={5}>
            1. OAuth 2.0 Client IDs set up in Google developer console
          </Box>
        </Typography>
        <Grid container spacing={5}>
          <Grid item xs={6}>
            <Paper elevation={8} className={classes.paper}>
              <Box height={400}>
                <Box pt={2}>
                  <img alt="google set up 1" src={GOOGLE_SETUP1} width="90%" />
                </Box>
              </Box>
            </Paper>
          </Grid>
          <Grid item xs={6}>
            <Paper elevation={8} className={classes.paper}>
              <Box height={400}>
                <Box pt={2}>
                  <img alt="google set up 2" src={GOOGLE_SETUP2} width="90%" />
                </Box>
              </Box>
            </Paper>
          </Grid>
        </Grid>
        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" fontWeight="fontWeightBold" mt={5}>
            2. Add dependency to NodeJS app
          </Box>
        </Typography>

        <Highlight language={'javascript'}>
          {'npm install passport passport-google-oauth20'}
        </Highlight>

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" fontWeight="fontWeightBold" mt={5}>
            3. Configure passportJS for Google
          </Box>
          <Box textAlign="Left" fontSize="body1.fontSize" pl={2}>
            You need your Google client id and client secret for the configuration. My application
            after successful authenticated, I will create or update the user model in mongo
            database. If you create a separated file to store below, you need to import it in you
            main app.js.
          </Box>
        </Typography>

        <Highlight language={'javascript'}>
          {'var passport = require("passport"); \n' +
            'var GoogleStrategy = require("passport-google-oauth20");\n' +
            'var User = require("../models/user-model");\n' +
            'var mongoose = require("mongoose");\n\n\n' +
            'passport.serializeUser((user, done) => {\n' +
            '  done(null, user.id);\n' +
            '});\n\n' +
            'passport.deserializeUser((id, done) => {\n' +
            '  User.findById(id).then((user) => {\n' +
            '    done(null, user);\n' +
            '  });\n' +
            '});\n\n' +
            'passport.use(\n' +
            '  new GoogleStrategy(\n' +
            '    {\n' +
            '      // Option for Google strategy\n' +
            '      callbackURL: "/auth/google/redirect",\n' +
            '      clientID: keys.google.clientID,\n' +
            '      clientSecret: keys.google.clientSecret,\n' +
            '    },\n' +
            '    (accessToken, refreshToken, profile, done) => {\n' +
            '      // Check user exists?\n' +
            '      User.findOne({\n' +
            '        googleId: profile.id,\n' +
            '      }).then((currentUser) => {\n' +
            '        if (currentUser) {\n' +
            '          if (currentUser.thumbnail !== profile._json.picture) {\n' +
            '            currentUser.thumbnail = profile._json.picture;\n' +
            '            currentUser.save().then((user) => {\n' +
            '              done(null, user);\n' +
            '            });\n' +
            '          } else {\n' +
            '            done(null, currentUser);\n' +
            '          }\n' +
            '        } else {\n' +
            '          console.log("Profile: " + JSON.stringify(profile));\n' +
            '          new User({\n' +
            '            _id: new mongoose.mongo.ObjectId(),\n' +
            '            userName: profile.displayName,\n' +
            '            googleId: profile.id,\n' +
            '            firstName: profile.name.givenName,\n' +
            '            lastName: profile.name.familyName,\n' +
            '            thumbnail: profile._json.picture,\n' +
            '            email: profile.emails[0].value.toLowerCase(),\n' +
            '          })\n' +
            '            .save()\n' +
            '            .then((newUser) => {\n' +
            '              done(null, newUser);\n' +
            '            });\n' +
            '        }\n' +
            '      });\n' +
            '    }\n' +
            '  )\n' +
            ');'}
        </Highlight>

        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" fontWeight="fontWeightBold" mt={5}>
            4. Configure routes to handle Oauth2
          </Box>
          <Box textAlign="Left" fontSize="body1.fontSize" pl={2}>
            Routes redirect to Google for authentication. I have two routes to handle mobile and web
            differently. The call back URLs for mobile and web are different. Because the handler
            for mobile and web is different after successfully authenticated by Google.
          </Box>
        </Typography>
        <Highlight language={'javascript'}>
          {'//auth with google - mobile\n' +
            'router.get(\n' +
            '  "/auth/google",\n' +
            '  passport.authenticate("google", {\n' +
            '    scope: ["profile", "email"],\n' +
            '  })\n' +
            ');\n\n' +
            '//auth with google - web\n' +
            'router.get(\n' +
            '  "/auth/google/web",\n' +
            '  passport.authenticate("google", {\n' +
            '    scope: ["profile", "email"],\n' +
            '    callbackURL: "/auth/google/redirect/web",\n' +
            '  })\n' +
            ');\n'}
        </Highlight>
        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" pl={2}>
            Call back handlers for mobile and web. The handler will redirect to above routes if
            failed the authentication. If successful, it will write to session to indicate the user
            has been authenticated. And it's short live (expies in 5 seconds) because the the
            redirect will happen right away. The following process will check this information to
            make sure the user is authenticated by Google. You can't establish the session here is
            because this is redirect from Google, the domain is different from my mobile and web
            domain. After creates the session attribute, it redirects to mobile or web for further
            processing.
          </Box>
        </Typography>
        <Highlight language={'javascript'}>
          {'// Callback route for google - mobile\n' +
            'router.get(\n' +
            '  "/google/redirect",\n' +
            '  passport.authenticate("google", { failureRedirect: "/auth/google" }),\n' +
            '  (req, res) => {\n' +
            '    client.set(req.user.id, "loggedIn", "EX", settings.redis.tokenExpire);\n\n' +
            '    res.redirect("MyApp://login?userid=" + req.user.id);\n' +
            '  }\n' +
            ');\n\n\n' +
            '// Callback route for google - web\n' +
            'router.get(\n' +
            '  "/google/redirect/web",\n' +
            '  passport.authenticate("google", {\n' +
            '    failureRedirect: "/auth/google/web",\n' +
            '    callbackURL: "/auth/google/redirect/web",\n' +
            '  }),\n' +
            '  (req, res) => {\n' +
            '    client.set(req.user.id, "loggedIn", "EX", settings.redis.tokenExpire);\n\n' +
            '    res.redirect(WEB_REDIRECT_URL + "?userid=" + req.user.id); \n' +
            '  }\n' +
            ');\n'}
        </Highlight>
        <Typography component="div" color="textSecondary">
          <Box textAlign="Left" fontSize="body1.fontSize" fontWeight="fontWeightBold" mt={5}>
            5. Callback redirect to the handler for after login
          </Box>
          <Box textAlign="Left" fontSize="body1.fontSize" pl={2}>
            As we mentioned above, this handler will get the request, and create the user session
            and continue. It's important to check the short live attribute before you establish user
            session. If you don't check that and it's called, it will just create the user session
            without passing the authentication. And that's it.
          </Box>
        </Typography>
      </Container>
    </React.Fragment>
  );
}
