import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
}));

export default function SimpleTabs(props) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const tabContents = props.tabContents;
  const tabLabels = props.tabLabels;

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const allTabs = tabLabels.map((tab, inx) => <Tab key={inx} label={tab} {...a11yProps(inx)} />);

  const allTabPanels = tabContents.map((content, inx) => (
    <TabPanel key={inx} value={value} index={inx}>
      {content}
    </TabPanel>
  ));

  if (allTabs && allTabs.length > 0) {
    return (
      <div className={classes.root}>
        <Tabs value={value} onChange={handleChange} aria-label="All tabs">
          {allTabs}
        </Tabs>

        {allTabPanels}
      </div>
    );
  } else {
    return <></>;
  }
}
